import React, { useState, useEffect, useRef } from "react";
import NavBar from "./Navbar/navbar";
import robosci from "./img/Robosci1.jpg";
import Lever from "./img/Lever.png";
import TL from "./img/LT.png";
import Carousel from "react-elastic-carousel";
import "./mainpage.css";
import { Container, Row, Card, Button } from "react-bootstrap";
import Col from "react-bootstrap/Col";
import GP1 from "./img/GameP1.mp4";
import GP2 from "./img/GameP2.mp4";
import GP3 from "./img/GameP3.mp4";
import Gl1 from "./img/robosci/LR (1).PNG";
import Gl2 from "./img/robosci/LR (2).PNG";
import Gl3 from "./img/robosci/LR (3).PNG";
import Gl4 from "./img/robosci/LR (4).PNG";
import Gl5 from "./img/robosci/Lever.jpg";
import Gl6 from "./img/robosci/Resistor.jpg";
import TK1 from "./img/TikTok (1).mp4";
import TK2 from "./img/TikTok (2).mp4";
import TK3 from "./img/TikTok (3).mp4";
import TK4 from "./img/TikTok (4).mp4";
import TK5 from "./img/TikTok (5).mp4";
import TK6 from "./img/TikTok (6).mp4";
import TK7 from "./img/TikTok (7).mp4";
import TK8 from "./img/TikTok (8).mp4";
import TK9 from "./img/TikTok (9).mp4";
import M1 from "./img/M (1).mp4";
import M2 from "./img/M (2).mp4";
import M3 from "./img/M (3).mp4";
import M4 from "./img/robosci/M (2).JPG";
import M5 from "./img/robosci/M (1).JPG";
import M6 from "./img/robosci/M (1).png";
import { NavLink } from "react-router-dom";

function App() {

  const [score, setScore] = useState(0);
  const [currentIndex, setCurrentIndex] = useState(0);

  const handleOptionClick = (selectedAnswer) => {
    if (selectedAnswer === slidequestion[currentIndex].correctAnswer) {
      setScore(score + 1);
    
    }
    if (currentIndex === slidequestion.length - 1) {
      setCurrentIndex(0);
     
    } else {
      setCurrentIndex(currentIndex + 1);
    }
  };
    

  const slidequestion = [
    {
      text: "ข้อที่ 1 : ลักษณะเฟืองแบบใดทำให้แรงบิดเพิ่มขึ้น",
      text1: "1/3",
      text2: "คะแนน : 1 ดาว",
      caption: "เฟืองใหญ่ทดเฟืองเล็ก",
      caption2: "เฟืองเล็กทดเฟืองใหญ่",
      correctAnswer: "เฟืองใหญ่ทดเฟืองเล็ก",
    },
    {
      text: "ข้อที่ 2 :  ลักษณะเฟืองแบบใดทำให้รอบหมุนเพิ่มขึ้น",
      text1: "2/3",
      text2: "คะแนน : 2 ดาว",
      caption: "เฟืองใหญ่ทดเฟืองเล็ก",
      caption2: "เฟืองเล็กทดเฟืองใหญ่",
      correctAnswer: "เฟืองเล็กทดเฟืองใหญ่",
    },
    {
      text: "ข้อที่ 3 : เฟืองตรงมักนำมาใช้ในระบบใด",
      text1: "3/3",
      text2: "คะแนน : 3 ดาว",
      caption: "ระบบส่งกำลัง",
      caption2: "ระบบไถล",
      correctAnswer: "ระบบส่งกำลัง",
    },
  ];

  return (
    <div
      className="body"
      style={{ height: "100vh", width: "100%", backgroundColor: "#FAFAFA " }}
    >
      <NavBar></NavBar>

      <Container>
        <Row style={{marginBottom:'20px'}}>
          <Col sm={12}>
            <Card className="fixed-top">
              <div className="BorderB" >
                <div className="Background" >
                  <div className="Head">
                    <NavLink exact to="/Addlession">
                      <Button
                        className="buttonM button1M"
                        style={{
                          marginRight: "20px",
                          float: "right",
                          marginTop: "50px",
                        }}
                      >
                        <h4 style={{ fontSize: "17px" }}>เพิ่มบทเรียน</h4>
                      </Button>
                    </NavLink>
                    <form
                      className="form"
                      style={{
                        marginRight: "-80px",
                        float: "right",
                        marginTop: "300px",
                        display:'flex'
                      }}
                    >
                      <abel>
                        <input
                          type="text"
                          name="name"
                          placeholder="Enter CODE"
                          style={{
                            marginLeft: "8px",
                            height: "100%",
                            width: "200px",
                            borderRadius: "5px 0px 0px 5px",
                            padding: "5px",
                            paddingLeft: "20px",
                            fontSize: "20px",
                            border: "none",
                          }}
                        />
                      </abel>
                      <input
                        type="submit"
                        value="Join"
                        style={{
                          height: "132%",
                          width: "100px",
                          borderRadius: "0px 5px 5px 0px",
                          padding: "5px",
                          fontSize: "20px",
                          border: "none",
                          background: "#8224e6",
                          color: "white",
                        }}
                      />
                    </form>
                  </div>
                </div>
              </div>
            </Card>
          </Col>
        </Row>

        <Row>
          <div
            style={{
              display: "flex",
              height: "max-content",
              backgroundColor: "#fff",
              paddingBottom: "5px",
            }}
          >
            <Col
              sm={6}
              className="left-containerM"
              style={{ marginBottom: "20px" }}
            >
              <Col sm={12}>
                <h3 style={{ marginTop: "20px", marginLeft: "10px" }}>
                  Lesson !
                </h3>
                <div
                  className="InContent"
                  style={{
                    padding: "0px",
                    paddingBottom: "0px",
                    backgroundColor: "#fff",
                  }}
                >
                  <div className="lesson4" style={{ marginBottom: "5px" }}>
                  <NavLink exact to="/Lever">
                  <button style={{backgroundColor:"#fff"}}>
                    <img
                      src={Gl5}
                      height="200px"
                      width="100%"
                      style={{ borderRadius: "10px 0px 0px 0px" }}
                    ></img>
                    </button>
                  </NavLink>

                  <NavLink exact to="/Pulley">
                  <button style={{backgroundColor:"#fff"}}>
                    <img
                      src={Gl2}
                      height="200px"
                      width="100%"
                      style={{ borderRadius: "0px 10px 0px 0px" }}
                    ></img>
                    </button>
                  </NavLink>
                   
                  
                  </div>
                  <div className="lesson4">

                  <NavLink exact to="/Gear">
                  <button style={{backgroundColor:"#fff"}}>
                    <img
                      src={Gl3}
                      height="200px"
                      width="100%"
                      style={{ borderRadius: "0px 0px 0px 10px" }}
                    ></img>
                    </button>
                  </NavLink>
                  <NavLink exact to="/Wheel">
                  <button style={{backgroundColor:"#fff"}}>
                  <img
                      src={Gl4}
                      height="200px"
                      width="100%"
                      style={{ borderRadius: "0px 0px 10px 0px" }}
                    ></img>
                  </button>
                  </NavLink>
                  
                  </div>
                </div>
              </Col>
            </Col>

            <Col sm={6} className="right-containerM">
              <h3 style={{ marginTop: "20px", marginLeft: "10px" }}>
                Recommend
              </h3>
              <Card className="OrightcontentM">
                <div
                  style={{
                    paddingRight: "10px",
                    display: "flex",
                    justifyContent: "space-between",
                  }}
                >
                    <Col sm={4}>
                    <Card className="rightcontentM">
                      <div className="InContentM">
                        <h3>ภารกิจ : การใช้งานสว่านไฟฟ้า</h3>
                        <div className="waitingM">
                          <video
                            className="VDOgear"
                            loop
                            autoPlay
                            muted
                            style={{ height: "300px", width: "100%" }}
                          >
                            <source src={GP2} type="video/mp4" />
                          </video>
                        </div>
                        <h5 style={{ marginTop: "10px" }}>
                          ต่อสายไฟเข้ากับสว่าน และมาทดลองใช้งานกัน
                        </h5>
                      </div>
                    </Card>
                  </Col>

                    <Col sm={4}>
                    <Card className="rightcontentM">
                      <div className="InContentM">
                        <h3>ภารกิจ : การไขน็อตด้วยไขควง</h3>
                        <div className="waitingM">
                          <video
                            className="VDOgear"
                            loop
                            autoPlay
                            muted
                            style={{ height: "300px", width: "100%" }}
                          >
                            <source src={GP3} type="video/mp4" />
                          </video>
                        </div>
                        <h5 style={{ marginTop: "10px" }}>
                          หยิบไขควง แล้วมาเรียนรู้วิธีไขน็อตด้วยไขควงกัน
                        </h5>
                      </div>
                    </Card>
                  </Col>

                  <Col sm={4}>
                    <Card className="rightcontentM">
                      <div className="InContentM">
                        <h3>ภารกิจ : การไขน็อตด้วยสว่านไฟฟ้า</h3>
                        <div className="waitingM">
                          <video
                            className="VDOgear"
                            style={{ height: "300px", width: "100%" }}
                            loop
                            autoPlay
                            muted
                          >
                            <source src={GP1} type="video/mp4" />
                          </video>
                        </div>
                        <h5 style={{ marginTop: "10px" }}>
                          เจาะแผ่นไม้ ใส่น็อต และไขน็อตด้วยสว่านไฟฟ้า
                        </h5>
                      </div>
                    </Card>
                  </Col>

                  

                

                </div>
              </Card>
            </Col>
            <Row></Row>
          </div>
        </Row>
        <Row>
          <Col sm={12}>
            <div className="review">
              <div
                className="InContent"
                style={{ padding: "35px 80px 35px 80px" }}
              >
                <h3 style={{ color: "black" }}>Snacks </h3>
                <Row
                  style={{ justifyContent: "space-between", padding: "5px" }}
                >
                  <Card
                    className="OrightcontentSNL"
                    style={{ backgroundColor: "#CAE6FF" }}
                  >
                    <div
                      style={{
                        display: "flex",
                        justifyContent: "space-between",
                      }}
                    >
                      <video className="VDOgearShort" controls ทำ>
                        <source src={TK1} type="video/mp4" />
                      </video>

                      <video className="VDOgearShort" controls>
                        <source src={TK2} type="video/mp4" />
                      </video>

                      <video className="VDOgearShort" controls>
                        <source src={TK3} type="video/mp4" />
                      </video>

                      <video className="VDOgearShort" controls>
                        <source src={TK4} type="video/mp4" />
                      </video>

                      <video className="VDOgearShort" controls>
                        <source src={TK5} type="video/mp4" />
                      </video>

                      <video className="VDOgearShort" controls>
                        <source src={TK6} type="video/mp4" />
                      </video>

                      <video className="VDOgearShort" controls>
                        <source src={TK7} type="video/mp4" />
                      </video>

                      <video className="VDOgearShort" controls>
                        <source src={TK8} type="video/mp4" />
                      </video>

                      <video className="VDOgearShort" controls>
                        <source src={TK9} type="video/mp4" />
                      </video>
                    </div>
                  </Card>
                </Row>
              </div>
            </div>
          </Col>
        </Row>
        <Row>
          <Col
            sm={12}
            style={{
              padding: "5px",
              paddingTop: "65px",
              paddingBottom: "65px",
              backgroundColor: "#fafafa",
            }}
          >
            <div className="RL">
              <Col sm={4}>
                <Card className="rightcontentR">
                  <div className="InContentM">
                    <h3>เฟืองตรงในห้องเกียร์</h3>
                    <video className="VDOMission" loop muted autoPlay>
                      <source src={M3} type="video/mp4" />
                    </video>
                  </div>
                </Card>
              </Col>

              <Col sm={4}>
                <Card className="rightcontentR">
                  <div className="InContentM">
                    <h3>การเคลื่อนที่ของกระเช้าไฟฟ้า</h3>
                    <video className="VDOMission" loop muted autoPlay>
                      <source src={M1} type="video/mp4" />
                    </video>
                  </div>
                </Card>
              </Col>

              <Col sm={4}>
                <Card className="rightcontentR">
                  <div className="InContentM">
                    <h3>แตะเพื่อนแล้วไฟดูด</h3>
                    <video className="VDOMission" loop muted autoPlay>
                      <source src={M2} type="video/mp4" />
                    </video>
                  </div>
                </Card>
              </Col>
            </div>
          </Col>
        </Row>
        <Row>
          {/* ///////////////// */}
          <Col sm={12}>
  <div className="review">
    <div className="InContent" style={{ padding: "35px 80px 35px 80px" }}>
      <h3 style={{ marginLeft: "40px", color: "black" }}>Review</h3>
      <Carousel>
        {slidequestion.map((slidequestion, index) => (
          <div key={index}>
            <div className="lessonM">
              <h5 style={{ float: "left", color: "#f29123" ,marginLeft:'10%'}}>
                {slidequestion.text1}
              </h5>
              <h5 style={{ float: "right", color: "#f29123" ,marginRight:'10%'}}>
                {slidequestion.text2}
              </h5>
              <h4 style={{marginTop:'20px'}}>{slidequestion.text}</h4>
              <div className="questionM" style={{marginTop:'30px'}}>
                <button
                  className="buttonL buttonLS"
                  onClick={() => handleOptionClick(slidequestion.caption)}
                  style={{ marginLeft: "20px" }}
                >
                  <h4 style={{ fontSize: "17px" }}>
                    {slidequestion.caption}
                  </h4>
                </button>
                <button
                  className="buttonL buttonLS"
                  onClick={() => handleOptionClick(slidequestion.caption2)}
                  style={{ marginRight: "20px" }}
                >
                  <h4 style={{ fontSize: "17px" }}>
                    {slidequestion.caption2}
                  </h4>
                </button>
              </div>
            </div>
          </div>
        ))}
      </Carousel>
      <h3 style={{ marginTop: "40px",color:'black',marginLeft: "40px" }}>Score: {score}/{slidequestion.length}</h3>
    </div>
  </div>
</Col>
          {/* /////////////////// */}
        </Row>
        <Row style={{ width: "100%" }}>
          <Col
            sm={12}
            style={{
              padding: "5px",
              paddingTop: "65px",
              paddingBottom: "65px",
              backgroundColor: "#fafafa",
            }}
          >
            <div className="RL">
              <Col sm={4}>
                <Card className="rightcontentR">
                  <div className="InContentM">
                    <h3>ล้อและเพลา การหมุนของชิงช้าสวรรค์</h3>
                    <img src={M6} className="VDOMission2"></img>
                  </div>
                </Card>
              </Col>

              <Col sm={4}>
                <Card className="rightcontentR">
                  <div className="InContentM">
                    <h3>มนุษย์พลังงาน</h3>
                    <img src={M5} className="VDOMission2"></img>
                  </div>
                </Card>
              </Col>

              <Col sm={4}>
                <Card className="rightcontentR">
                  <div className="InContentM">
                    <h3>ฟ้าแลบแต่ไม่ไมีไฟฟ้าร้อง</h3>
                    <img src={M4} className="VDOMission2"></img>
                  </div>
                </Card>
              </Col>
            </div>
          </Col>
        </Row>
      </Container>
    </div>
  );
}

export default App;
