import React, { useState } from "react";
import { NavLink } from "react-router-dom";
import "./login.css";
import Logo from "./img/robo.png";
const Login = () => {
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");

  const handleLogin = (event) => {
    event.preventDefault();
    // Handle login logic here
  };

  return (
    <div className="logindiv" style={{width:'600px'}}>
      <h2>Login</h2>
      <form onSubmit={handleLogin}>
        <label>Email:</label>
        <input
          type="email"
          value={email}
          onChange={(event) => setEmail(event.target.value)}
          required
        />
        <label>Password:</label>
        <input
          type="password"
          value={password}
          onChange={(event) => setPassword(event.target.value)}
          required
        />
        <NavLink exact to="/Dashboard">
          <button type="submit">Login</button>
        </NavLink>
      </form>
    </div>
  );
};

const Register = () => {
  const [firstName, setFirstName] = useState("");
  const [lastName, setLastName] = useState("");
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [verificationCode, setVerificationCode] = useState("");
  const [role, setRole] = useState("");

  const handleRegister = (event) => {
    event.preventDefault();
    // Handle registration logic here
  };

  return (
    <div className="logindiv" style={{width:'1300px'}}>
      <h2>Register</h2>
      <form onSubmit={handleRegister}>
        <div style={{display:'flex' ,    justifyContent:'space-between'}}>
          
        <div style={{marginRight:'30px'}}>
          <label>First Name:</label>
          <input
            type="text"
            value={firstName}
            onChange={(event) => setFirstName(event.target.value)}
            required
          />
          <label>Last Name:</label>
          <input
            type="text"
            value={lastName}
            onChange={(event) => setLastName(event.target.value)}
            required
          />
          <label>Email:</label>
          <input
            type="email"
            value={email}
            onChange={(event) => setEmail(event.target.value)}
            required
          />
        </div>

        <div style={{marginLeft:'30px'}}>
          <label>Password:</label>
          <input
            type="password"
            value={password}
            onChange={(event) => setPassword(event.target.value)}
            required
          />
          <label>Verification Code:</label>
          <input
            type="text"
            value={verificationCode}
            onChange={(event) => setVerificationCode(event.target.value)}
            required
          />
          <label>Role:</label>
          <select
            value={role}
            onChange={(event) => setRole(event.target.value)}
          >
            <option value="">Select a role</option>
            <option value="teacher">Teacher</option>
            <option value="student">Student</option>
            <option value="parent">Parent</option>
          </select>
        </div>
        </div>
       

        <button className="LIbutton" type="submit">
          Register
        </button>

      </form>
    </div>
  );
};

const LoginRegisterPage = () => {
  const [isLogin, setIsLogin] = useState(true);

  return (
    <div
      className="body"
      style={{ height: "100vh", width: "100%", backgroundColor: "#f3fffd " }}
    >
      {" "}
      <div style={{ textAlign: "center", marginTop: "30px" }}>
        <img src={Logo} height="300px" width="auto"></img>
      </div>
      <div className="logindiv">
        {isLogin ? <Login /> : <Register />}
        <button
          style={{ padding: " 10px 20px" }}
          onClick={() => setIsLogin(!isLogin)}
        >
          {isLogin ? "Register" : "Login"}
        </button>
      </div>
    </div>
  );
};

export default LoginRegisterPage;
