import React from "react";
import { BrowserRouter as Router, Routes,Route } from "react-router-dom";
import Gear from './components/lessonGear'
import Lever from './components/lessonLever'
import Wheel from './components/lessonWheel'
import Pulley from './components/lessonPulley'
import Home from './components/mainpage'
import Addlession from './components/addLession'
import Profile from './components/Profile'
import Testform from './components/testform'
import Testadd from './components/addlessiontest'
import Getfire from './components/getdata'
import Login from './components/login'


function App() {
  return  (
    <Router>
        <Routes>
            <Route path="/" element = {<Login />} />
            <Route path="/Gear" element = {<Gear />} />
            <Route path="/Lever" element = {<Lever />} />
            <Route path="/Wheel" element = {<Wheel />} />
            <Route path="/Pulley" element = {<Pulley />} />
            <Route path="/Addlession" element = {<Addlession />} />
            <Route path="/Profile" element = {<Profile />} />
            <Route path="/Testform" element = {<Testform />} />
            <Route path="/Addlession/Getfire" element = {<Getfire />} />
            <Route path="/Testadd" element = {<Testadd  />} />
            <Route path="/Dashboard" element = {<Home  />} />
        </Routes>
    </Router>
  );
}
  
  export default App;