import React, { useState, useEffect, useRef } from "react";
import { Container, Row, Col, Card, Button } from "react-bootstrap";
// import { db } from "./firebase";
import { useNavigate } from "react-router-dom";

import firebase from "firebase/compat/app";
import { NavLink } from "react-router-dom";
  import arrow from "./img/arrow.png";
import "firebase/compat/firestore";
import "firebase/compat/storage";

const firebaseConfig = {
  apiKey: "AIzaSyDLWbRGMZtEXWg1Oitpp2aKVcs_4ci4bGQ",
  authDomain: "robosci-database.firebaseapp.com",
  projectId: "robosci-database",
  storageBucket: "robosci-database.appspot.com",
  messagingSenderId: "624881008510",
  appId: "1:624881008510:web:ca473280789b835d78fd2f",
  measurementId: "G-NSLYGWTFDT",
};

firebase.initializeApp(firebaseConfig);

// Get references to the Firestore and Storage services
const db = firebase.firestore();
const storage = firebase.storage();

var countquestion = 1;
var countsubtitle = 1;
var counttest = 1;
var countknowledge = 1;

function App() {
  const [questions, setQuestions] = useState([
    { question: "", questionpoint: "", answerTrue: "", answerfalse: "" },
  ]);
  const [subtitles, setSubtitles] = useState([{ subtitle: "" }]);
  const [testtitle, setTest] = useState([
    { testtitle: "", testpoint: "", testtime: "" },
  ]);
  const [knowledge, setKnowledge] = useState([
    { knowledge: "", knowledgelink: "" },
  ]);
  const [fieldCount, setFieldCount] = useState(1);

  const [videoFile, setVideoFile] = useState(null);
  const [uploadProgress, setUploadProgress] = useState(0);



  const handleFileChange = (event) => {
    setVideoFile(event.target.files[0]);
  };

  const [loader, setLoader] = useState(false);

  const handleUpload = () => {
    const file = document.getElementById("file-input").files[0];
    const storageRef = firebase.storage().ref(); // Get a reference to the storage service
    const videoRef = storageRef.child(`videos/${file.name}`);
    const uploadTask = videoRef.put(file);

    uploadTask.on(
      "state_changed",
      (snapshot) => {
        const progress = Math.round(
          (snapshot.bytesTransferred / snapshot.totalBytes) * 100
        );
        setUploadProgress(progress);
        console.log(`Upload progress: ${progress}%`);
      },
      (error) => {
        console.error(error);
      },
      () => {
        uploadTask.snapshot.ref.getDownloadURL().then((downloadURL) => {
          console.log(`File available at ${downloadURL}`);
          db.collection("videos").add({
            downloadURL,
            name: file.name,
            timestamp: firebase.firestore.FieldValue.serverTimestamp(),
          });
        });
      }
    );
  };

  let navigate = useNavigate();

  const handleSubmit = (event) => {
    event.preventDefault();
    const addLessonRef = db.collection("testtest");
    const subtitlearray = [];
    questions.forEach((q) => {
      addLessonRef.doc(`question-${countquestion}`).set({
        question: q.question,
        questionpoint: q.questionpoint,
        answerTrue: q.answerTrue,
        answerfalse: q.answerfalse,
      });
      countquestion = countquestion + 1;
    });
    subtitles.forEach((s) => {
      const Allsubtitle = s.subtitle;
      subtitlearray.push(Allsubtitle);
      addLessonRef.doc("subtitle").set({
        subtitlearray: subtitlearray,
      });
    });
    testtitle.forEach((t) => {
      addLessonRef.doc(`test-${counttest}`).set({
        testtitle: t.testtitle,
        testpoint: t.testpoint,
        testtime: t.testtime,
      });
      counttest = counttest + 1;
    });
    knowledge.forEach((k) => {
      addLessonRef
        .doc(`knowledge-${countknowledge}`)
        .set({
          knowledge: k.knowledge,
          knowledgelink: k.knowledgelink,
        })
        .then(() => {
          navigate("./Getfire");
          setLoader(false);
          alert("Your message has been submitted👍");
        })
        .catch((error) => {
          alert(error.message);
          setLoader(false);
        });
      countknowledge = countknowledge + 1;
    });

    setQuestions([
      { question: "", questionpoint: "", answerTrue: "", answerfalse: "" },
    ]);
    setSubtitles([{ subtitle: "" }]);
    setTest([{ testtitle: "", testpoint: "" }]);
    setKnowledge([{ knowledge: "", knowledgelink: "" }]);
    setFieldCount(1);
  };

  /////////////////////////////////////////////////////////
  const handleAddQuestionField = () => {
    setFieldCount(fieldCount + 1);
    setQuestions([
      ...questions,
      { question: "", questionpoint: "", answerTrue: "", answerfalse: "" },
    ]);
  };

  const handleQuestionChange = (index, value) => {
    const newQuestions = [...questions];
    newQuestions[index].question = value;
    setQuestions(newQuestions);
  };

  const handleQuestionPointChange = (index, value) => {
    const newQuestions = [...questions];
    newQuestions[index].questionpoint = value;
    setQuestions(newQuestions);
  };

  const handleAnswerTrueChange = (index, value) => {
    const newQuestions = [...questions];
    newQuestions[index].answerTrue = value;
    setQuestions(newQuestions);
  };

  const handleAnswerFalseChange = (index, value) => {
    const newQuestions = [...questions];
    newQuestions[index].answerfalse = value;
    setQuestions(newQuestions);
  };

  const handleDeleteQuestionField = (index) => {
    const newQuestions = [...questions];
    newQuestions.splice(index, 1);
    setQuestions(newQuestions);
    setFieldCount(fieldCount - 1);
  };
  ////////////////////////////////////////////////////////////////////////////////////////////////////////
  const handleAddSubtitleField = () => {
    setFieldCount(fieldCount + 1);
    setSubtitles([...subtitles, { subtitle: "" }]);
  };

  const handleSubtitleChange = (index, value) => {
    const newSubtitles = [...subtitles];
    newSubtitles[index].subtitle = value;
    setSubtitles(newSubtitles);
  };

  const handleDeleteSubtitleField = (index) => {
    const newSubtitles = [...subtitles];
    newSubtitles.splice(index, 1);
    setSubtitles(newSubtitles);
    setFieldCount(fieldCount - 1);
  };
  ////////////////////////////////////////////////////////////////////////////////////////////////////////
  const handleAddTestField = () => {
    setFieldCount(fieldCount + 1);
    setTest([...testtitle, { testtitle: "", testpoint: "", testtime: "" }]);
  };

  const handleTesttitleChange = (index, value) => {
    const newTesttitle = [...testtitle];
    newTesttitle[index].testtitle = value;
    setTest(newTesttitle);
  };

  const handleTestPointChange = (index, value) => {
    const newTesttitle = [...testtitle];
    newTesttitle[index].testpoint = value;
    setTest(newTesttitle);
  };

  const handleTesttimeChange = (index, value) => {
    const newTesttitle = [...testtitle];
    newTesttitle[index].testtime = value;
    setTest(newTesttitle);
  };

  const handleDeleteTesttitleField = (index) => {
    const newTesttitle = [...questions];
    newTesttitle.splice(index, 1);
    setTest(newTesttitle);
    setFieldCount(fieldCount - 1);
  };
  ////////////////////////////////////////////////////////////////////////////////////////////
  const handleAddknowledgeField = () => {
    setFieldCount(fieldCount + 1);
    setKnowledge([...knowledge, { knowledge: "", knowledgelink: "" }]);
  };

  const handleknowledgeChange = (index, value) => {
    const newknowledge = [...knowledge];
    newknowledge[index].knowledge = value;
    setKnowledge(newknowledge);
  };

  const handleknowledgelinkChange = (index, value) => {
    const newknowledge = [...knowledge];
    newknowledge[index].knowledgelink = value;
    setKnowledge(newknowledge);
  };

  const handleDeletknowledgeField = (index) => {
    const newknowledge = [...knowledge];
    newknowledge.splice(index, 1);
    setKnowledge(newknowledge);
    setFieldCount(fieldCount - 1);
  };
  //////////////////////////////////////////////////////////////////////////////
  const renderQuestionFields = () => {
    return questions.map((q, index) => (
      <div
        key={index}
        className="border"
        style={{
          backgroundColor: "#fff",
          width: "100%",
          marginTop: "10px",
          marginRight: "20px",
        }}
      >
        <div
          style={{
            display: "flex",
            justifyContent: "space-between",
            alignItems: "center",
            paddingtop: "10px",
          }}
        >
          <h4 style={{ margin: "10px 0px" }}>Question {index + 1}</h4>
          <Button
            className="button button1M "
            style={{ width: "140px", borderRadius: "10px" }}
            variant="danger"
            onClick={() => handleDeleteQuestionField(index)}
          >
            Delete Question
          </Button>
        </div>
        <div style={{ margin: "10px 0px" }}>
          <input
            type="text"
            value={q.question}
            onChange={(e) => handleQuestionChange(index, e.target.value)}
            className="form-control"
            placeholder="Enter your question"
            required
            style={{
              height: "60px",
              width: "560px",
              borderRadius: "5px 5px 5px 5px",
              paddingLeft: "20px",
              borderColor: "#8f8f8f",
              fontSize: "22px",
            }}
          />
        </div>
        <div style={{ margin: "10px 0px" }}>
          <label>Question Point: </label>
          <input
            type="number"
            value={q.questionpoint}
            onChange={(e) => handleQuestionPointChange(index, e.target.value)}
            className="form-control"
            placeholder="Only Number"
            required
            style={{
              height: "30px",
              width: "110px",
              borderRadius: "5px 5px 5px 5px",
              paddingLeft: "20px",
              borderColor: "#8f8f8f",
              fontSize: "14px",
            }}
          />
        </div>
        <div
          style={{
            margin: "10px 0px",
            display: "flex",
            justifyContent: "space-between",
          }}
        >
          <input
            type="text"
            value={q.answerTrue}
            onChange={(e) => handleAnswerTrueChange(index, e.target.value)}
            className="form-control"
            placeholder="Enter True answer"
            required
            style={{
              height: "40px",
              width: "40%",
              borderRadius: "5px 5px 5px 5px",
              paddingLeft: "20px",
              borderColor: "#8f8f8f",
              fontSize: "17px",
            }}
          />
          <input
            type="text"
            value={q.answerfalse}
            onChange={(e) => handleAnswerFalseChange(index, e.target.value)}
            className="form-control"
            placeholder="Enter False answer"
            style={{
              height: "40px",
              width: "40%",
              borderRadius: "5px 5px 5px 5px",
              paddingLeft: "20px",
              borderColor: "#8f8f8f",
              fontSize: "17px",
            }}
            required
          />
        </div>
      </div>
    ));
  };

  const renderSubtitleFields = () => {
    return subtitles.map((s, index) => (
      <div
        key={index}
        className="border"
        style={{
          backgroundColor: "#fff",
          width: "650px",
          marginTop: "10px",
          marginRight: "20px",
        }}
      >
        <div
          style={{
            display: "flex",
            justifyContent: "space-between",
            alignItems: "center",
            paddingtop: "10px",
          }}
        >
          <h4 style={{ margin: "10px 0px" }}>Subtitle {index + 1}</h4>
          <Button
            className="button button1M "
            style={{ width: "140px", borderRadius: "10px" }}
            variant="danger"
            onClick={() => handleDeleteSubtitleField(index)}
          >
            Delete Subtitle
          </Button>
        </div>
        <div style={{ margin: "10px 0px" }}>
          <input
            required
            type="text"
            value={s.subtitle}
            onChange={(e) => handleSubtitleChange(index, e.target.value)}
            className="form-control"
            placeholder="Enter subtitle"
            style={{
              height: "60px",
              width: "560px",
              borderRadius: "5px 5px 5px 5px",
              paddingLeft: "20px",
              borderColor: "#8f8f8f",
              fontSize: "22px",
            }}
          />
        </div>
      </div>
    ));
  };

  const renderTesttitleFields = () => {
    return testtitle.map((t, index) => (
      <div
        key={index}
        className="border"
        style={{
          backgroundColor: "#fff",
          width: "650px",
          marginTop: "10px",
          marginRight: "20px",
        }}
      >
        <div
          style={{
            display: "flex",
            justifyContent: "space-between",
            alignItems: "center",
            paddingtop: "10px",
          }}
        >
          <h4 style={{ margin: "10px 0px" }}>Test {index + 1}</h4>
          <Button
            className="button button1M "
            style={{ width: "140px", borderRadius: "10px" }}
            variant="danger"
            onClick={() => handleDeleteTesttitleField(index)}
          >
            Delete Test
          </Button>
        </div>

        <div style={{ margin: "10px 0px" }}>
          <input
            required
            type="text"
            value={t.subtitle}
            onChange={(e) => handleTesttitleChange(index, e.target.value)}
            className="form-control"
            placeholder="Enter subtitle"
            style={{
              height: "60px",
              width: "560px",
              borderRadius: "5px 5px 5px 5px",
              paddingLeft: "20px",
              borderColor: "#8f8f8f",
              fontSize: "22px",
            }}
          />
          <br></br>
          <div style={{ display: "flex", justifyContent: "space-between" }}>
            <div>
              <label>Test Point: </label>
              <input
                type="number"
                value={t.testpoint}
                onChange={(e) => handleTestPointChange(index, e.target.value)}
                className="form-control"
                placeholder="Only Number"
                required
                style={{
                  height: "30px",
                  width: "110px",
                  borderRadius: "5px 5px 5px 5px",
                  paddingLeft: "20px",
                  borderColor: "#8f8f8f",
                  fontSize: "14px",
                  marginTop: "10px",
                }}
              />
            </div>
            <div style={{ marginRight: "5d%" }}>
              <label>Time: </label>
              <input
                type="number"
                value={t.testtime}
                onChange={(e) => handleTesttimeChange(index, e.target.value)}
                className="form-control"
                placeholder="Only Number"
                required
                style={{
                  height: "30px",
                  width: "110px",
                  borderRadius: "5px 5px 5px 5px",
                  paddingLeft: "20px",
                  borderColor: "#8f8f8f",
                  fontSize: "14px",
                  marginTop: "10px",
                }}
              />
            </div>
          </div>
          <button
            className="button button1"
            style={{
              height: "50px",
              width: "580px",
              margin: "10px 0px 10px 0px",
            }}
          >
            <h4 style={{ fontSize: "18px" }}>Choose file</h4>
          </button>
        </div>
      </div>
    ));
  };

  const renderknowledgeFields = () => {
    return knowledge.map((k, index) => (
      <div
        key={index}
        className="border"
        style={{
          backgroundColor: "#fff",
          width: "650px",
          marginTop: "10px",
          marginRight: "20px",
        }}
      >
        <div
          style={{
            display: "flex",
            justifyContent: "space-between",
            alignItems: "center",
            paddingtop: "10px",
          }}
        >
          <h4 style={{ margin: "10px 0px" }}>Knowledge {index + 1}</h4>
          <Button
            className="button button1M "
            style={{ width: "140px", borderRadius: "10px" }}
            variant="danger"
            onClick={() => handleDeletknowledgeField(index)}
          >
            Delete knowledge
          </Button>
        </div>

        <div style={{ margin: "10px 0px" }}>
          <input
            required
            type="text"
            value={k.knowledge}
            onChange={(e) => handleknowledgeChange(index, e.target.value)}
            className="form-control"
            placeholder="Enter knowledge title"
            style={{
              height: "90px",
              width: "560px",
              borderRadius: "5px 5px 0px 0px",
              paddingLeft: "20px",
              borderColor: "#8f8f8f",
              fontSize: "22px",
            }}
          />
          <input
            required
            type="text"
            value={k.knowledgelink}
            onChange={(e) => handleknowledgelinkChange(index, e.target.value)}
            className="form-control"
            placeholder="Enter knowledge link"
            style={{
              height: "35px",
              width: "560px",
              borderRadius: "0px 0px 5px 5px",
              paddingLeft: "20px",
              borderColor: "#8f8f8f",
              fontSize: "18px",
              marginTop: "-5px",
            }}
          />
        </div>
      </div>
    ));
  };

  const wordArray = [
    "เฟืองตรงจะมีลักษณะทรงกลมมีฟันเฟืองอยู่ด้านบนของวงกลม",
    "เป็นเฟืองที่มีฟันขนานกับแกนหมุน",
    "เป็นเฟืองที่มีการใช้งานกันมากที่สุด มักนำมาใช้ในระบบส่งกำลัง เช่น เครื่องจักร",
    "ระบบส่งกำลังรถยนต์ภายในห้องเกียร์ จะมีฟันเฟืองโลหะหลายขนาดต่างกันไปตามความเร็วที่ต้องใช้",
    "หลักการทำงานจะมีการส่งกำลังหมุนจากเพลาหนึ่งไปยังอีกเพลาหนึ่ง",
    "โดยถ้าเฟืองใหญ่ทดเฟืองเล็ก จะทำให้รอบหมุนเร็วขึ้น แต่ถ้าเฟืองเล็กทดเฟืองใหญ่ จะได้แรงบิด",
  ];
  const wordNumber = ["1/6", "2/6", "3/6", "4/6", "5/6", "6/6"];
  const [currWord, setCurrWord] = useState(wordArray[0]);
  const [currWord1, setCurrWord1] = useState(wordNumber[0]);
  const [isActive, setIsActive] = useState(true);

  const index = useRef(0);
  useEffect(() => {
    let interval = null;
    if (isActive) {
      interval = setInterval(() => {
        index.current++;
        setCurrWord(wordArray[index.current]);
        setCurrWord1(wordNumber[index.current]);
        if (index.current === wordArray.length - 1) {
          setIsActive(false);
        }
      }, 5000);
    }
    return () => clearInterval(interval);
  });

  ////////////////////////////////////////                        START                    /////////////////////////////////////////////////////////////////////////////////////////////////
  return (
    <div
      style={{ height: "100vh", width: "100%", backgroundColor: "#f3fffd " }}
    >
      <Container>
        <form onSubmit={handleSubmit}>
          <Row>
            <Col sm={12}>
              <Card className="fixed-top" style={{ height: "360px" }}>
                <button
                  type="submit"
                  className="button button1"
                  style={{
                    float: "right",
                    height: "50px",
                    width: "200px",
                    borderRadius: "10px",
                    margin: "20px",
                  }}
                >
                  Finish
                </button>
                <div
                  style={{
                    height: "360px",
                    width: "100%",
                    background: "#f3fffd",
                    textAlign: "gggtt",
                  }}
                >
                  <div
                    style={{
                      height: "360px",
                      width: "800px",
                      background: "#E5F9F6",
                      marginLeft: "auto",
                      marginRight: "auto",
                      textAlign: "center",
                    }}
                  >
                    <NavLink exact to="/Dashboard">
                <button
                  className="button button1"
                  style={{
                    position: "relative",
                    top: "-0",
                    left: "-800px",
                    width: "60px",
                  }}
                >
                  <img
                    src={arrow}
                    height="45px"
                    width="auto"
                    style={{ marginTop: "-2px", marginRight: "5px" }}
                  ></img>
                </button>
              </NavLink>
                    <div>
                      
                      <button className="button button1"
                      style={{
                        width: "80%",
                        height: "50px",
                        margin: "120px 10px 10px 10px",
                      }} onClick={handleUpload} >Upload</button>
                      <input type="file" id="file-input" onChange={handleFileChange} style={{margin :"10px 10px 70px 10px"}} />
                      {uploadProgress > 0 && (
                        <h4 >Upload progress: {uploadProgress}%</h4>
                      )}
                </div>
                  </div>
                </div>
              </Card>
            </Col>
          </Row>

          <Row className="content">
            <Col sm={6} className="left-container">
              <Col sm={12}>
                <Card className="leftcontent" style={{ marginTop: "0px" }}>
                  <div className="InContent">
                    <h3>คำบรรยาย</h3>
                    <div style={{ padding: "10px 30px 50px 10px " }}>
                      <div className="borderAdd">{renderSubtitleFields()}</div>
                      <Button
                        className="button button1"
                        variant="primary"
                        onClick={handleAddSubtitleField}
                        style={{ float: "right", marginRight: "-25px" }}
                      >
                        <h4 style={{ fontSize: "17px" }}>Add Subtitle</h4>
                      </Button>
                    </div>
                  </div>
                </Card>
              </Col>

              <Col sm={12}>
                <Card className="leftcontent">
                  <div className="InContent">
                    <h3>ภารกิจ</h3>
                    <div
                      className="border"
                      style={{
                        textAlign: "center",
                        margin: "2px 50px 20px 50px",
                      }}
                    >
                      <h3>เพิ่ม / สร้างภารกิจ</h3>
                      <Button
                        className="button button1"
                        style={{ width: "180px" }}
                      >
                        <h4 style={{ fontSize: "18px" }}>Let's Go</h4>
                      </Button>
                    </div>
                  </div>
                </Card>
              </Col>

              <Col sm={12}>
                <Card className="leftcontent">
                  <div className="InContent">
                    <h3>คำบรรยาย</h3>
                    <div style={{ padding: "10px 30px 50px 10px " }}>
                      <div className="borderAdd">{renderknowledgeFields()}</div>
                      <Button
                        className="button button1"
                        variant="primary"
                        onClick={handleAddknowledgeField}
                        style={{ float: "right", marginRight: "-25px" }}
                      >
                        <h4 style={{ fontSize: "17px" }}>Add knowledge</h4>
                      </Button>
                    </div>
                  </div>
                </Card>
              </Col>
            </Col>

            <Col sm={6} className="right-container">
              <Col sm={12}>
                <Card className="rightcontent" style={{ marginTop: "0px" }}>
                  <div className="InContent">
                    <h3>คำถามทดสอบความเข้าใจ</h3>
                    <div className="borderAdd" style={{ width: "88%" }}>
                      {renderQuestionFields()}
                    </div>
                    <Button
                      className="button button1"
                      variant="primary"
                      onClick={handleAddQuestionField}
                      style={{
                        marginRight: "20px",
                        float: "right",
                        marginTop: "-25%",
                        borderRadius: "100%",
                        height: "50px",
                        width: "50px",
                      }}
                    >
                      <h4 style={{ fontSize: "60px", marginTop: "-25px" }}>
                        +
                      </h4>
                    </Button>
                  </div>
                </Card>
              </Col>

              <Col sm={12}>
                <Card
                  className="rightcontent"
                  style={{
                    backgroundColor: "#f3fffd",
                    boxShadow: "0px 0px 0px 0px #8f8f8f",
                  }}
                >
                  <div className="InContent">
                    <h3>ทำสถิติให้ดีที่สุด</h3>
                    <div className="borderAdd" style={{ width: "88%" }}>
                      {renderTesttitleFields()}
                    </div>
                    <Button
                      className="button button1"
                      variant="primary"
                      onClick={handleAddTestField}
                      style={{
                        marginRight: "20px",
                        float: "right",
                        marginTop: "-25%",
                        borderRadius: "100%",
                        height: "50px",
                        width: "50px",
                      }}
                    >
                      <h4 style={{ fontSize: "60px", marginTop: "-25px" }}>
                        +
                      </h4>
                    </Button>
                  </div>
                </Card>
              </Col>
            </Col>
          </Row>
        </form>
       
      </Container>
    </div>
  );
}

export default App;
