import React, { useState, useEffect, useRef } from 'react';
import { Container, Row, Col, Card, Button } from "react-bootstrap";
import Carousel from 'react-elastic-carousel';
import Gear from './img/gear2.mp4';
import ADS from './img/Lego.jpg';
import './lesson.css'
import Lever from './img/Lever.png'
import LeverT from './img/LT.png'
import pulley from './img/spu.jpg'
import Unity from './img/Unity.png'
import T1 from './img/T1.jpg'
import T2 from './img/T2.jpg'
import T3 from './img/T3.jpg'
import { db } from "./firebase";
import T4 from './img/T4.jpg'



function App() {

  function sayHello() {
    alert('เยี่ยมไปเลย');
  }

const [data, setData] = useState([]);
  const [subarray, setSubarray] = useState([]);


  
  useEffect(() => {
    const fetchData = async () => {
      const response = await db.collection("testtest").get();
      const data = response.docs.map((doc) => doc.data());
      const subtitleSnapshot = await db.collection("testtest").doc("subtitle").get();
      const Allsubtitle = subtitleSnapshot.data().subtitlearray;
      setSubarray(Allsubtitle);
      setData(data);
    };
    fetchData();
  }, []);

  const wordArray = subarray;
  const wordNumber = Array.from(
    { length: wordArray.length },
    (_, i) => `${i + 1}/${wordArray.length}`
  );
  const [currWord, setCurrWord] = useState(wordArray[0]);
  const [currWord1, setCurrWord1] = useState(wordNumber[0]);
  const [isActive, setIsActive] = useState(true);
  const index = useRef(0);

  useEffect(() => {
    let interval = null;
    if (isActive) {
      interval = setInterval(() => {
        if (index.current+1 == wordArray.length ) {
          setIsActive(false);
        }
        setCurrWord(wordArray[index.current]);
        setCurrWord1(wordNumber[index.current]);

        index.current = (index.current +  1) % wordArray.length;
        
        console.log("index = ",index.current)
        console.log("wordArray = ",wordArray.length)

        
      }, 3000);
    }
    return () => clearInterval(interval);
  }, [isActive, wordArray, wordNumber]);

  const imglever = Lever;
  const imgleverT = LeverT;
  const imgpulley = pulley;

  const sliderecommend = [
    {
      text: 'คาน',
      imgL : imglever,
      imgT : imgleverT,

      text1: 'รอก',
      imgL1 : imgpulley,
      imgT1 : imgleverT,
    },
    {
      text: 'ล้อและเพลา',
      imgL : imglever,
      imgT : imgleverT,

      text1: 'รอก',
      imgL1 : imglever,
      imgT1 : imgleverT,      
    },   
  ];

  return (
    <div className='' style={{height:"100vh",width:"100%",backgroundColor:"#f3fffd "}}>
    <Container>
      <Row>
        <Col sm={12}>
          <Card className="fixed-top">
            <video className="VDOgear" controls loop autoPlay> 
              <source src={Gear} type="video/mp4" /> 
            </video>
          </Card>
        </Col>
      </Row>

      <Row className="content">
        <Col sm={6} className="left-container">

          <Col sm={12} >
            <Card className="leftcontent" style={{marginTop:'0px'}}>
              <div className='InContent'>
                <h5 style={{marginLeft:'5px',float:'right',color:'#f29123'}}>{currWord1}</h5>
                <h3>คำบรรยาย</h3>
                <h4 style={{marginLeft:'5px'}}>{currWord}</h4>
              </div>
            </Card>
          </Col>

          <Col sm={12} >
            <Card className="leftcontent">
              <div className='InContent'>
                <h3>ภารกิจ</h3>
                  <img height='auto' width='100%' src={Unity} style={{borderRadius:'10px'}}></img>
              </div>
            </Card>
          </Col>

          <Col sm={12} >
            <Card className="leftcontent">
              <img src={ADS} height='auto' width='100%' style={{borderRadius:'10px'}}></img>
            </Card>
          </Col>

          <Col sm={12} >
            <Card className="leftcontent">
              <div className='InContent'>
              <h3>ความรู้เพิ่มเติม</h3>
              <div className='Borderlesson' style={{height:'200px'}}>

              {data.map((item, index) => (
                        item.knowledge != null ? (
                        <div key={index}>   
                  <div className='borderL'>
                    <h4 style={{fontSize:'17px'}}>{item.knowledge}</h4>
                    <a href = {item.knowledgelink} style={{float:'right',marginRight:'20px',color:'#f29123'}}><h5>เรียนรู้เพิ่มเติม</h5></a>
                  </div>
              </div>
              ) : null
            ))} 
            </div>
            </div>
            </Card>
          </Col>

        </Col>

        <Col sm={6} className="right-container">

          <Col sm={12} >
            <Card className="rightcontent" style={{marginTop:'0px'}}>
              <div className='InContent'>
              <h3>คำถามทดสอบความเข้าใจ</h3>
                <Carousel>

                    {data.map((item, index) => (
                        item.question != null ? (
                        <div key={index}>   
                      <div className='border'>
                        <h4>{item.question}</h4>
                        
                        {/* <h5 style={{float:'left',color:'#f29123'}}>{slidequestion.text1}</h5> */}
                        <h5 style={{float:'right',color:'#f29123'}}>คะแนน : {item.questionpoint}</h5>
                        <div className='question'>
                          <button className='button button1' onClick={sayHello} style={{marginLeft:'20px'}} ><h4 style={{fontSize:'17px'}}>{item.answerTrue}</h4></button>
                          <button className='button button1' onClick={sayHello} style={{marginRight:'20px'}} ><h4 style={{fontSize:'17px'}}>{item.answerfalse}</h4></button>  
                        </div>
                      </div>
                    </div>
                        ) : null
                    
                  ))} 
                </Carousel>
              </div>  
            </Card>
          </Col>

          <Col sm={12} >
            <Card className="rightcontent" style={{backgroundColor:'#f3fffd',boxShadow:'0px 0px 0px 0px #8f8f8f'}}>
              <div className='InContent' style={{paddingTop:'15px'}}>
              <h3 style={{marginLeft:'40px'}}>สร้างสถิติควิซที่ดีที่สุดให้ได้!!!</h3>
                  <Carousel>
                  {data.map((item, index) => (
                        item.testtitle != null ? (
                        <div key={index}>   
                          <div style={{marginTop:'8px'}}>
                            <div className='lesson' style={{backgroundColor:'#fff',boxShadow:'0px 0px 1px 0.1px #8f8f8f'}}>
                            <div style={{marginTop:'5px'}}><h4>{item.testtitle}</h4>
                            <h5 style={{float:'left',color:'#f29123'}}>เวลา : {item.testtime} นาที</h5>
                            <h5 style={{float:'right',color:'#f29123'}}>เต็ม {item.testpoint}  คะแนน</h5>
                              <div style={{textAlign:"center"}}>
                                <button className='buttonT button2' onClick={sayHello} ><h4 style={{fontSize:'20px'}}>ทำข้อสอบ</h4></button>
                              </div>
                            </div>
                            </div>
                          </div>
                        </div>
                               ) : null
                      ))} 
                  </Carousel>
              </div>
            </Card>
          </Col>

          <Col sm={12} >
            <Card className="leftcontent">
              <div className='InContent'>
                <h3>บทเรียน</h3>
                <div className='Borderlesson'>
                  <div className='Mylesson'>
                    <div height='300px' width='100px' style={{display:"flex"}}>
                      <div style={{textAlign:'center'}}>
                        <img height='auto' width='80px' src={T1} style={{backgroundColor:'#333',borderRadius:'10px',}}></img>
                      </div>
                      <div height='100px' width='100px' style={{paddingTop:'10px',marginLeft:'20px'}}>
                        <h4>เฟืองตรง (spur gears)</h4>
                        <h5>อาจารย์ กิตติพันธ์  อุดมเศรษฐ์</h5>
                      </div>
                      <div height='100px' width='100px' style={{paddingTop:'20px',paddingLeft:'0px',marginLeft:'auto'}}>
                        <h4 style={{color:'#f29123'}}>73%</h4>
                      </div>
                    </div>
                  </div>  

                  <div className='Mylesson'>
                    <div height='300px' width='100px' style={{display:"flex"}}>
                      <div style={{textAlign:'center'}}>
                        <img height='auto' width='80px' src={T2} style={{backgroundColor:'#333',borderRadius:'10px',}}></img>
                      </div>
                      <div height='100px' width='100px' style={{paddingTop:'10px',marginLeft:'20px'}}>
                        <h4>เฟืองสะพาน (rack gears)</h4>
                        <h5>อาจารย์ ไพรรุ่ง  งามสมพรพงศ์ </h5>
                      </div>
                      <div height='100px' width='100px' style={{paddingTop:'20px',paddingLeft:'0px',marginLeft:'auto'}}>
                        <h4 style={{color:'#f29123'}}>53%</h4>
                      </div>
                    </div>
                  </div>

                  <div className='Mylesson'>
                    <div height='300px' width='100px' style={{display:"flex"}}>
                      <div style={{textAlign:'center'}}>
                        <img height='auto' width='80px' src={T3} style={{backgroundColor:'#333',borderRadius:'10px',}}></img>
                      </div>
                      <div height='100px' width='100px' style={{paddingTop:'10px',marginLeft:'20px'}}>
                        <h4>เฟืองวงแหวน (internal gears)</h4>
                        <h5>อาจารย์ โสภิณ  ประกอบของ</h5>
                      </div>
                      <div height='100px' width='100px' style={{paddingTop:'20px',paddingLeft:'0px',marginLeft:'auto'}}>
                        <h4 style={{color:'#f29123'}}>11%</h4>
                      </div>
                    </div>
                  </div>

                  <div className='Mylesson'>
                    <div height='300px' width='100px' style={{display:"flex"}}>
                      <div style={{textAlign:'center'}}>
                        <img height='auto' width='80px' src={T4} style={{backgroundColor:'#333',borderRadius:'10px',}}></img>
                      </div>
                      <div height='100px' width='100px' style={{paddingTop:'10px',marginLeft:'20px'}}>
                        <h4>เฟืองเฉียง (helical gears)</h4>
                        <h5>อาจารย์ ธัญญธร  ปรีชารัตน์</h5>
                      </div>
                      <div height='100px' width='100px' style={{paddingTop:'20px',paddingLeft:'0px',marginLeft:'auto'}}>
                        <h4 style={{color:'#f29123'}}> 89%</h4>
                      </div>
                    </div>
                  </div>

                  <div className='Mylesson'>
                    <div height='300px' width='100px' style={{display:"flex"}}>
                      <div style={{textAlign:'center'}}>
                      <img height='auto' width='80px' src={T1} style={{backgroundColor:'#333',borderRadius:'10px',}}></img>
                      </div>
                      <div height='100px' width='100px' style={{paddingTop:'10px',marginLeft:'20px'}}>
                        <h4>เฟืองดอกจอก (bevel gears)</h4>
                        <h5>ครู A</h5>
                      </div>
                      <div height='100px' width='100px' style={{paddingTop:'20px',paddingLeft:'0px',marginLeft:'auto'}}>
                        <h4 style={{color:'#f29123'}}>5%</h4>
                      </div>
                    </div>
                  </div>

                  <div className='Mylesson'>
                    <div height='300px' width='100px' style={{display:"flex"}}>
                      <div style={{textAlign:'center'}}>
                        <img height='auto' width='80px' src={T2} style={{backgroundColor:'#333',borderRadius:'10px',}}></img>
                      </div>
                      <div height='100px' width='100px' style={{paddingTop:'10px',marginLeft:'20px'}}>
                        <h4>เฟืองตัวหนอน (worm gears)</h4>
                        <h5>ครู A</h5>
                      </div>
                      <div height='100px' width='100px' style={{paddingTop:'20px',paddingLeft:'0px',marginLeft:'auto'}}>
                        <h4 style={{color:'#f29123'}}>34%</h4>
                      </div>
                    </div>
                  </div>

                  <div className='Mylesson'>
                    <div height='300px' width='100px' style={{display:"flex"}}>
                      <div style={{textAlign:'center'}}>
                        <img height='auto' width='80px' src={T3} style={{backgroundColor:'#333',borderRadius:'10px',}}></img>
                      </div>
                      <div height='100px' width='100px' style={{paddingTop:'10px',marginLeft:'20px'}}>
                        <h4>เฟืองเกลียวสกรู(spiral gears)</h4>
                        <h5>ครู A</h5>
                      </div>
                      <div height='100px' width='100px' style={{paddingTop:'20px',paddingLeft:'0px',marginLeft:'auto'}}>
                        <h4 style={{color:'#f29123'}}>50%</h4>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </Card>
          </Col>

          <Col sm={12} >
            <Card className="rightcontent">
              <div className='InContent'>
              <h3>แนะนำบทเรียน</h3>
                  <Carousel>
                      {sliderecommend.map((sliderecommend, index)=> (
                        <div key={index} className ='BRCard'>
                          
                          <div className='recommend' style={{marginRight:'20px'}}>
                            <button className='buttonR button3' onClick={sayHello} >
                                <img height='170px' width = 'auto' src = {sliderecommend.imgT}></img>
                                <img height='auto' width = '180px' src = {sliderecommend.imgL}></img>
                            </button>
                            <div className='Rcard'>
                              <h4>{sliderecommend.text}</h4>
                            </div>
                          </div> 

                          <div className='recommend'>
                            <button className='buttonR button3' onClick={sayHello} >
                                <img height='170px' width = 'auto' src = {sliderecommend.imgT1}></img>
                                <img height='auto' width = '180px' src = {sliderecommend.imgL1}></img>
                            </button>
                            <div className='Rcard'>
                              <h4>{sliderecommend.text1}</h4>
                            </div>
                          </div>

                        </div>
                      ))} 
                  </Carousel>
              </div>
            </Card>
          </Col>
          
        </Col>  
      </Row>

    </Container>
    </div>
  );
}

export default App;
