// export default App;


import React, { useState, useEffect } from "react";
import { db } from "./firebase";

function App() {
  const [sentence, setSentence] = useState("");
  const [KLLink, setKLLink] = useState("");
  const [KLTitle, setKLTitle] = useState("");
  const [status, setStatus] = useState("unread");
  const [loader, setLoader] = useState(false);
  const [isSelected, setIsSelected] = useState(false);
  
  const [knowledge, setKnowledge] = useState([
    { knowledge: "", knowledgelink: "" },
  ]);

  
  // useEffect to retrieve data from Firestore
  useEffect(() => {
    const unsubscribe = db
      .collection("TestKnow")
      .where("status", "==", "read")
      .where("sentence", "==", sentence)
      .onSnapshot((querySnapshot) => {
        querySnapshot.forEach((doc) => {
          setKLLink(doc.data().knowledgeLink);
          setKLTitle(doc.data().knowledgeTitle);
        });
      });
    console.log(KLLink);
    return unsubscribe;
  }, [sentence]);

  const handleKeyword = (e) => {
    e.preventDefault();
    setLoader(true);

    db.collection("TestKnow")
      .doc(sentence)
      .set({
        sentence: sentence,
        status: status,
        knowledgeLink: KLLink,
        knowledgeTitle: KLTitle,
      })
      .then(() => {
        setLoader(false);
      })
      .catch((error) => {
        alert(error.message);
        setLoader(false);
      });

    setSentence("");
    setKLLink("");
    setKLTitle("");
    setStatus("unread");
  };

  return (
    <div>
      <form onSubmit={handleKeyword}>
        <label>Enter your Keyword</label>
        <input
          placeholder="Keyword"
          value={sentence}
          onChange={(e) => setSentence(e.target.value)}
          required
        />
        <button
          type="submit"
          style={{
            height: "50px",
            width: "200px",
            borderRadius: "10px",
            margin: "20px",
          }}
        >
          ค้นหา
        </button>
      </form>
      {KLTitle &&
  KLTitle.map((KLTitles, index) => {

    const handleBoxSelect = () => {
      const selectedBox = {
        knowledge: KLTitles,
        knowledgelink: KLLink[index],
      };
      db.collection("KnowLedgeTest")
        .add(selectedBox)
        .then(() => {
          console.log("Selected box added to Firestore:", selectedBox);
        })
        .catch((error) => {
          alert(error.message);
        });
      setIsSelected(true);
    };

    const handleBoxUnselect = () => {
      setIsSelected(false);
    };

    return (
      <div className="leftcontent" key={index}>
        <div className="InContent">
          <h3>ความรู้เพิ่มเติม</h3>
          <div className="Borderlesson" style={{ height: "200px" }}>
            <div className="borderL">
              <h4 style={{ fontSize: "17px" }}>{KLTitles}</h4>
              <a
                href={KLLink[index]}
                style={{
                  float: "right",
                  marginRight: "20px",
                  color: "#f29123",
                }}
              >
                <h5>เรียนรู้เพิ่มเติม</h5>
              </a>
              {!isSelected ? (
                <button onClick={handleBoxSelect}>Select this box</button>
              ) : (
                <button onClick={handleBoxUnselect}>Unselect this box</button>
              )}
            </div>
          </div>
        </div>
      </div>
    );
  })}
    </div>
  );
}

export default App;
